.query_header > h1{
    text-align: center;
    font-size: 40px;
    font-family: 'Nobile', sans-serif;
    color: #6d4c41;
}
.query{
    background-color: white;
    padding: 30px;
   border-radius: 10px;
   margin:0px 20px;
   box-shadow: 0px 5px 8px -9px rgba(0.75,0.70,0.72,0.75);
}
.query_info{
    text-align: center;
    justify-content: center;
    display: flex;
flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    display: grid;
    place-items: center;
}
.query_image > img{
    height: 300px;
    margin: 0px 10px;


}
.query_image{
    justify-content: center;
}
.query_text{
    width: 100%;
    text-align: center;
    justify-content: center;
}