.header{
    position: sticky;
    z-index: 200;
    top:0;
    background-color: #6d4c41;
    padding:10px;
    height: 50px;
    display: flex;
    justify-content: space-between;
    box-shadow: 0px 5px 8px -9px rgba(0,0,0,0.75);
}
.MuiDrawer-paperAnchorRight{
    background-color:rgb(211, 204, 163) !important;
    width: 50%;
}

.header_logo > img{
height: 60px;
cursor: pointer;
margin-top: 10px;
z-index: 200 !important;
}
.header_logo > img:hover{
    transform: scale(1.07);
    cursor: pointer;
}
.header_link{
    font-size: 15px;
    text-transform: uppercase;
        color: white;
    display: flex;
padding-right: 30px;
margin-bottom: 20px;
}
.anchor_icon{
    display: flex;
    align-items: center;
    color: white;
}
.anchor{
    margin-right: 25px;
    padding-top:2px;
  padding-left: 3px;
    cursor: pointer;
   font-weight: 700;
   font-family: 'Nobile', sans-serif;
   color:white;
}
.anchor:hover{
color: black;
}
.anchor_icon:hover{
    color: black;
}