
.about_header > h1{
    text-align: center;
    font-size: 40px;
    font-family: 'Nobile', sans-serif;
    color: #6d4c41;
}
.about_info{
    display: grid;
    place-items: center !important;
}
.about_text > h2{
    font-size: 30px;
    font-weight: 600;
    font-family: 'Fredoka One', cursive;
}
.about{
    background-color: white;
    padding: 30px;
   border-radius: 10px;
   margin:0px 20px;
   box-shadow: 0px 5px 8px -9px rgba(0.75,0.70,0.72,0.75);
}
.about_info{
    text-align: center;
    justify-content: center;
    display: flex;
flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}
.about_image > img{
   
    object-fit: contain;
    margin: 0px 10px;


}
.about_image{
    justify-content: center;
}
.about_text{
    width: 60%;
    text-align: center;
    justify-content: center;
}
