.footer{
    width: 100%;
    height: 300px;
    background: chocolate;
    display: grid;
    place-items: center;
    padding: 20px 0px;
}
.footer_link{
    width: 80%;
    height: 90%;
    background-color:rgba(255,255,255,.75);
    border-radius: 3px;
    padding: 10px;
    
}
.footer_social{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 10px;
}
.footer_social > .MuiSvgIcon-root{
    color: grey;
    cursor: pointer;
    font-size: 30px;
}
.footer_social > .MuiSvgIcon-root:hover{
    color: white;
}
.footer_copywrite{
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: center;
}
.footer_copywrite > p{
    color: gray;
}
.footer_anchor{
    display: flex;
    align-items: center;
    justify-content: space-evenly;

}
a{
    color: black;
}
a:hover{
    color: white;
    text-decoration:underline;
}
.footer_anchor1 > p:hover{
text-decoration: underline;
cursor: pointer;
color: white;
}
.footer_anchor2 > p:hover{
    text-decoration: underline;
    cursor: pointer;
    color: white;
    }