.call{
    margin-top: 30px;
    display: grid;
    place-items: center;
    transform: scale(1.07);
  
}
.call_svg > img{
    height: 250px;
}
.call_box{
    display: grid;
    place-items: center;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    padding: 50px;
  
}
.call_title > h2{
    font-size: 30px;
}
.call_text{
    font-size: 25px;
}
.call_icon > .MuiSvgIcon-root{
    font-size: 65px;
}