.contact{
    display: grid;
    place-items: center;
}
.contact_svg > img{
    height: 300px;
}
.contact_box{
    display: grid;
    justify-content: center;
    place-items: center;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 5px 8px -9px rgba(0.75,0.70,0.72,0.75);
  width: 90%;
  padding: 20px 5px;

}
.contact_title > h2{
    font-size: 30px;
}
.contact_text{
    font-size: 25px;
}
.contact_icon > .MuiSvgIcon-root{
    font-size: 65px;
}